<template>
  <div class="chat-container">
    <!--  -->
    <van-nav-bar
      title=""
      left-arrow
      @click-left="$router.back()"
      fixed
    />
    <!-- / -->

    <!--  -->
    <div class="message-list" ref="message-list">
      <div
        class="message-item"
        :class="{ reverse: item.isMe }"
        v-for="(item,index) in msgList "
        :key="index"
      >
        <van-image
          class="avatar"
          slot="icon"
          round
          width="30"
          height="30"
          src="https://img.yzcdn.cn/vant/cat.jpeg"
        />
        <div class="title">
          <span>{{item.msg}}</span>
        </div>
      </div>
    </div>
    <!-- / -->

    <!--  -->
    <van-cell-group class="send-message">
      <van-field v-model="message" center clearable>
        <van-button slot="button" size="small"
        @click="onSend"
        type="primary"></van-button>
      </van-field>
    </van-cell-group>
    <!-- / -->
  </div>
</template>

<script>
import io from 'socket.io-client'
import { getItem, setItem } from '@/utils/storage'
export default {
  name: 'UserChat',
  data () {
    return {
      message: '',
      socket: '',
      msgList: getItem('chat-msg') || []
    }
  },
  created () {
    // //socket
    const socket = io('http://ttapi.research.itcast.cn')

    this.socket = socket// 

    socket.on('connect', function () { // 
      // console.log('')
    })

    //  socket.emit('type', content)
    // window.socket = socketlog

    //  socket.on('type', data => console.log(data))
    // socket.on('message', msg => console.log('message =>', msg))
    // 
    socket.on('message', msg => this.msgList.push(msg))
  },
  methods: {
    onSend () {
      const message = this.message
      if (!message.length) { // 
        return
      }
      // **  ** isMe=true
      const data = { msg: message, timestamp: Date.now(), isMe: true }// 
      this.socket.emit('message', data)// 

      // 
      this.msgList.push(data)

      this.message = '' // 
    },
    toBottom () {
      const container = this.$refs['message-list']
      container.scrollTop = container.scrollHeight
    }
  },
  watch: {
    // 1  2 
    msgList (value) {
      // 
      setItem('chat-msg', value)

      this.$nextTick(() => {
        this.toBottom()
      })
      //   this.toBottom()// 

    /**
     * $nextTick()
     * 
     * dom 
     * api  
     * 
     */
    }
  }
}
</script>

